<template>
  <div id="main">
    <!-- <video id="video" autoplay style="display: none;"></video> -->
  </div>
</template>

<script>
import { World } from '@/three/main'

export default {
  name: 'Main',
  data: function() {
    return {
      world: null
    }
  },
  mounted: function () {
    this.world = new World();
    console.log(this.world)
    const container = document.getElementById("main")
    container.appendChild(this.world.renderer.domElement)

    window.addEventListener('resize', this.processResize)
  },
  methods: {
    processResize() {
        this.world.setSize();
    }
  }
}
</script>

